import { useEffect, useState } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import FormCalendarInput from "../../../../Ui/Input/FormCalendarInput";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../../Utils/Functions/dateUtility";
import { useTranslation } from "react-i18next";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { useFeedbackUpdateCloseDate } from "../../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import dayjs from "dayjs";

const UpdateStudentFeedbackLastDate = ({
  onClose,
  ifid,
  onRefetch,
  closeDate,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    feedback_close_at: "",
  });
  const [feedbackUpdateCloseDate] = useFeedbackUpdateCloseDate();

  useEffect(() => {
    if (closeDate) {
      setState((prev) => ({
        ...prev,
        feedback_close_at: dayjs(closeDate)?.format("DD/MM/YYYY"),
      }));
    }
  }, [closeDate]);

  const onDateGrab = (val) => {
    setState((prev) => ({
      ...prev,
      feedback_close_at: slashDateFormat(val),
    }));
  };
  const onConfirm = () => {
    if (ifid) {
      setDisabled((pre) => !pre);
      feedbackUpdateCloseDate({
        ifid: ifid,
        feedbackUpdate: {
          feedback_close_at: ymdSlashDateFormat(state?.feedback_close_at),
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    }
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("update_last_date")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <FormCalendarInput
            label={t("feedback_close_date")}
            placeholder={t("select_date")}
            name="calendar"
            value={state.feedback_close_at}
            onChange={onDateGrab}
          />
          <CreateButton label={t("update")} onAction={onConfirm} />
        </UserMemberContentWrapper>
        {disabled && <QvipleLoader />}
      </div>
    </Modal>
  );
};

export default UpdateStudentFeedbackLastDate;
